	export default {
		data() {
			return {
				term_no: '',
				redBalls: [],
				blueBall:'',
				tableData:[
					{prizeType:'一等奖',amount: '0',total: '0'},
					{prizeType:'二等奖',amount: '11',total: '15836'},
					{prizeType:'三等奖',amount: '145',total: '2402'},
					{prizeType:'四等奖',amount: '578',total: '200'},
					{prizeType:'五等奖',amount: '5313',total: '50'},
					{prizeType:'六等奖',amount: '10222',total: '10'},
					{prizeType:'七等奖',amount: '65456',total: '5'}
					
				]
			}
		},
		methods: {
			
		},
		onLoad(options) {
			var that = this;
            //that.data = JSON.parse(options.index); // 字符串转对象
            that.term_no = options.term_no;
			var arr = options.index.split(";");
			var redBalls = [];
			var blueBall = '';
			for (var i = 0; i <= arr.length-2; i++) {
				redBalls.push(arr[i]);
			}
			blueBall = arr[arr.length-1];
			that.redBalls=redBalls;
			that.blueBall=blueBall;
			console.log(that.redBalls);
			console.log(that.blueBall);
			
        }
	}
